const actions={
	sentmsg({commit},item){			
		commit('sentmsg',item)
	},
	addnumber({commit,state},num){
		commit('addnumber',num)
	},
	reducenumber({commit,state},num){
		commit('reducenumber',num)
	},
	deletegood({commit,state},num){
		commit('deletegood',num)
	}
}
export default actions


