<template>
	<div class="detailbanner">
		<div class="swiper-container-detail">	
			<div class="swiper-wrapper">
	            <div class="swiper-slide">
	            	<img :src="''||detailmsg.src1"/>
	            </div>
	            <div class="swiper-slide">
	            	<img :src="''||detailmsg.src2"/>
	            </div>
	            <div class="swiper-slide">
	            	<img :src="''||detailmsg.src3"/>
	            </div>
	            <div class="swiper-slide">
	            	<img :src="''||detailmsg.src4"/>
	            </div>
	       </div>
		</div>
		<div class="swiper-pagination-detail"></div>
		<div class="content">
			<p>{{''||detailmsg.title1}}</p>
			<p>
				<span class="newprice">{{''||detailmsg.new}}</span>
				<span class="oldprice">{{''||detailmsg.old}}</span>
			</p>
		</div>
		<div class="detailtext">
			<p class="flowerword">花语：</p>
			<p class="flowercontent">{{''||detailmsg.title2}}</p>
		</div>	
	</div>
</template>

<script>
	import axios from 'axios'
	var detailbanner = null
	export default{
		props:['sonMsg'],
		data(){
			return{
				detailmsg:''
			}
		},
		methods:{
			addsrc(){
				var that = this
				axios.get('static/json/details1.json').then(function(item){
					item.data.forEach(function(item,i){					
						if( that.sonMsg==item.id ){
							that.detailmsg=item
						}
					})
				})
			}
		},
		mounted(){
			this.addsrc()
			detailbanner = new Swiper('.swiper-container-detail',{
				 pagination:'.swiper-pagination-detail',
		        autoplay: 2500,
		        paginationClickable :true,
		        touchMoveStopPropagation : false
			})
		}
	}
</script>

<style scoped lang="scss">
	.detailbanner{
		width:100%;
		font-size:12px;
		position:relative;
		.swiper-container-detail{
			width:100%;
			img{
				width:100%;
			}
		}
		.swiper-pagination-detail{

			position: absolute;
			z-index:10000;
			left:45%;
			top:70%;
			.swiper-pagination-bullet {
				margin-left:3%!important;
			}
		}
		.content{
			display:flex;
			flex-direction: column;
			justify-content: space-between;
			font-size:14px;
			width:100%;
			height:80px;
			padding:3% 4%;
			border-bottom:8px solid #eee ;
			.newprice{
				color:#f60;
				font-size:20px;
			}
			.oldprice{
				font-size:12px;
				color:#9b9b9b;
				text-decoration: line-through;
			}
		}
		.detailtext{
			display:flex;
			flex-direction: row;
			justify-content: space-between;
			font-size:12px;
			width:100%;
			height:70px;
			padding:3% 4%;
			border-bottom:8px solid #eee ;
			.flowerword{
				width:10%;
				line-height:40px;
			}
			
			.flowercontent{
				width:88%;
				color:#9b9b9b;
			}
		}
	}
</style>


