import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

import Showcar from '../components/Showcar'
import Mine from '../components/Mine/Hello'
import Waimai from '../components/Waimai'
import List from '../components/class/class1'
import Detail from '../components/detail/detail'

export default new Router({
  routes: [
    {
      path: '/mine',
      name: 'mine',
      component: Mine
    },
    {
    	path:'/showcar/:num',
    	name:'showcar',
    	component:Showcar
    },
    {
    	path:'/detail/:id',
    	name:'detail',
    	component:Detail
    },
    {
    	path:'/list',
    	name:'list',
    	component:List
    },
    {
    	path:'/waimai',
    	name:'waimai',
    	component:Waimai
    },
    {
    	path:'/',
    	redirect:'/waimai'
    },
    {
    	path:'*',
    	redirect:'/waimai'
    }
  ]
})



