<template>
  <div class="list_main">
    <ul>
      <li v-for="(item,$index) in items" @click="selectStyle (item, $index, item.id)"  :class="{'active':item.active}"> {{item.name}}</li>
    </ul>
  </div>
</template>

<script>
	import Vue from 'vue'
	import axios from "axios"
	import Bus from "./eventbus"
	const classtwo = Vue.extend({
					data(){
						return {
							active:false,
							items:[
								{name:"鲜花",id:1},
								{name:"永生花",id:2},
								{name:"巧克力",id:3},
								{name:"品牌公仔",id:4},
								{name:"绿植花卉",id:5},
								{name:"礼篮",id:6}
							],
							message:''
						}
					},
				  methods:{
				  	selectStyle (item, index ,msg) {
				  		 var that= this
				　　　　　this.$nextTick(function () {
				　　　　　　　this.items.forEach(function (item) {
				　　　　　　　　　Vue.set(item,'active',false);
				　　　　　　　});
				　　　　　　　Vue.set(item,'active',true);
				　　　　　});
								axios.get('./static/json/flower.json').then(function(items){
									switch(msg){
										case 1:that.message = items.data.flower ;break;
										case 2:that.message = items.data.gift ;break;
										case 3:that.message = items.data.oldman ;break;
										case 4:that.message = items.data.flower ;break;
										case 5:that.message = items.data.gift ;break;
										case 6:that.message = items.data.oldman ;break;
									}
							 })
						},
				　},
				watch:{
					message(){
						var that = this
							setTimeout(()=>{
							Bus.$emit("givemsg",that.message)			
						},0)
					}
				}
					
	})
	
	Vue.component('class-list',classtwo)
	export default classtwo
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
ul{
	  width: 100%;
    float: left;
    overflow-y: scroll;
    background-color: rgba(245,245,245,.9); 
    li{
    	line-height: 48px;
    	text-align: center;
    	font-size: 13px;
    }
    
}
.list_main ul li.active{
    	background: white;
    	color: red;
    }
.list_main{
	width:25%;
	float:left;
}
</style>



