<template>
  <div class="list">
    <div id="box">
    	<img id="logo" :src="lists.logo"/>
    	<p></p>
    	<ul>
    		<li v-for="list in lists.list">
    			<img :src="list.src"/>
    			<span>{{list.name}}</span>
    		</li>
    	</ul>
    </div>
  </div>
</template>

<script>
	
	import axios from 'axios'
	import Vue from 'vue'
	import Bus from "./eventbus"
	const classthree = Vue.extend({
		name: 'list',
		data () {
			return {
				lists:[]
			}
		},
		created(){
			var that = this
			Bus.$on('givemsg',function(data){
				that.lists=data
			})
		},
		mounted(){
			var that = this
				axios.get('./static/json/flower.json').then(function(items){
					 that.lists = items.data.flower 
				})
		}
	})
	Vue.component('class-detail',classthree)
	export default classthree
</script>

<style scoped lang="scss">
.list{
	display: block;
	width:75%;
	height:100%;
	float:left;
	background: white;
	overflow-y: auto;
	#box{
		width:92%;
		height:100%;
		margin-top:10px;
		margin-left: 4%;
		p{
			font-size: 12px;
			line-height: 50px;
		}
		#logo{
			
			width:100%;
		}
		ul li{
			display: flex;
			flex-direction: column;
			justify-content: space-around;
			float: left;
	    width: 33.3%;
	    font-size: 12px;
	    text-align: center;
	    margin-bottom:5%;
	    img{
	    	width:80%;
	    	margin:0 auto;
	    	display: block;
	    }
		}
	}
}
</style>



