<template>
	<div class="select">
		<div class="title">
			<div class="title_line"></div>
			<p class="title_name">精选特惠</p>
		</div>
		<div class="swiper-container-two">	
			<div class="swiper-wrapper">
	            <router-link :to='"/detail/"+good.id' class="swiper-slide" v-for="(good,index) in goodsimage" :key="index">
	            	<img :src="good.src"/>
	            	<p>
	            		<span>{{good.Class1}}</span>:
	            		<span>{{good.Cpmc}}</span>
	            	</p>
	            	<p>
	            		<span>￥{{good.Yhj}}</span>
	            		<span class="old">￥{{good.Jg}}</span>
	            	</p>
	            </router-link>
	      	</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	var banner=null;
	export default {
		data(){
			return{
				goodsimage:null
			}
		},
		methods:{
			getadds(){
				var that = this
				axios.get('static/json/goods.json').then(function(item){
					that.goodsimage=item.data
				})
			}
		},
		mounted(){
			this.getadds()
			banner = new Swiper('.swiper-container-two',{
				slidesPerView: 3,
				centeredSlides: true,
				loopedSlides :8,
				touchMoveStopPropagation : false
			})
		},
		watch:{
			goodsimage(){
				setTimeout(()=>{
					banner.update()
				},0)				
			}
		}
	}
	
</script>

<style scoped lang="scss">
	.select{
		padding-bottom: 20px;
		border-bottom: 8px solid #eee;
	}
	.swiper-container-two{
		.swiper-wrapper{
			font-size:12px;
			.swiper-slide{
				display:block;
				margin:0 16px 0 14px;
				width:30%!important;
				img{
					width:130px;
				}
				p{
					text-align: center;
					margin-top:10px;
					.old{
						color:#aaa;
						text-decoration: line-through;
					}
				}
			}
		}
	}
	.title_line {
	    width: 160px;
	    height: 1px;
	    background-color: #bbb;
	    margin: 22px auto 0px;
	}
	.title_name {
	    width: 90px;
	    text-align: center;
	    background-color: #fff;
	    font-size: 16px;
	    color: #222;
	    margin: -12px auto 13px;
	}
</style>


