<template>
	<div id="detail-all">
		<div>
			<detail-banner :son-msg="cid"></detail-banner>
			<banner-foot></banner-foot>	
		</div>
		<detail-footer></detail-footer>
	</div>
</template>

<script>
	import Allbanner from "../allbanner.vue"
	import detailBanner from "./detailbanner"
	import detailFooter from './detailfooter'
	export default{
		data(){
			return{
				cid:''
			}
		},
		
		created(){
			this.cid = this.$route.params.id
		},
		mounted(){
			setTimeout(function(){
				var detailScroll = new iScroll("detail-all",{})
		  	},500)
		},
		components:{
			detailBanner,
			Allbanner,
			detailFooter
		}
	}
</script>

<style lang="scss" scoped>
	#detail-all{
		height:100%;
	}
</style>


