<template>
	<div class="allbanner">
		<div class="swiper-container-allbanner">	
	       	<div class="swiper-pagination">
		       	<span v-for="img in imgsrc">
		       		<img :src="img.src2"/>
		       		<p>{{img.text}}</p>
		       	</span>
	       	</div>
			<div class="swiper-wrapper">
	            <div class="swiper-slide" v-for="img in imgsrc">
	            	<img :src="img.src3"/>
	            </div>
	       	</div>
		</div>
		<p>专注鲜花20年，2016年获评“鲜花行业龙头企业”</p>
	</div>
</template>

<script>
	import Vue from 'vue'
	import axios from 'axios'
	var allbanner = null
	const Allbanner = Vue.extend({
		name:'footer',
		data(){
			return{
				imgsrc:null
			}
		},
		methods:{
			addimg(){
				var that = this
				axios.get('static/json/allbanner.json').then(function(item){
					that.imgsrc = item.data
				})
			}
		},
		mounted(){
			this.addimg()
			allbanner = new Swiper('.swiper-container-allbanner',{
		        autoplay: 2500,
		        loop:true,
		        paginationClickable :true,
		        touchMoveStopPropagation : false
			})
		},
		watch:{
			imgsrc(){
				setTimeout(()=>{
					allbanner.update()
				},0)				
			}
		}
	})
	Vue.component('banner-foot',Allbanner)
	export default Allbanner
</script>

<style scoped lang="scss">
	.allbanner{
		padding-bottom: 50px;
		width:100%;
		font-size:12px;
		text-align: center;
		p{
			padding:2% 0 2% 0;
		}
		.swiper-container-allbanner{
			position: relative;
			font-size: 12px;
			width:100%;
			.swiper-wrapper{
				padding-top:80px;
				width:100%;
				.swiper-slide{
					width:96%;
					padding:0 3% 0 3%;
					img{
						width:100%;
					}
				}
			}
			.swiper-pagination{
				position:absolute;
				top:0;
				left:0;
				width:100%;
				height:95px;
				display: flex;
				flex-direction: row;
				justify-content: space-around;
				span{
					display:inline-block;
					p{
						text-align: center;
					}
					img{
						width:48px;
						height:48px;
						margin:5%;
					}
				};
			}
		}
	}
</style>


