<template>
	<div id="detailfooter">
		<ul class="other">
			<li>
				<a href="#">
					<span class="iconfont icon-kefu"></span>
					<span>客服</span>	
				</a>
			</li>
			<i class="middle_line"></i>
			<li>
				<a href="#">					
					<span class="iconfont icon-dianhua"></span>
					<span>电话</span>
				</a>
			</li>
			<i class="middle_line"></i>
			<li class="numposition">
				<a href="#">
					<span class="iconfont icon-gouwuche1"></span>
					<span>购物车</span>
					<span class="num">2</span>
				</a>
			</li>
		</ul>
		<ul class="add">
			<li @click="sentmsg">
				<router-link to="/showcar/1" class="addcar">加入购物车</router-link>
			</li>
			<li>
				<router-link to="" class="buy">立即购买</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
	
	import axios from 'axios'
	export default{
		data(){
			return{
				msg:this.$route.params.id,
				msg1:{}
			}
		},
		
		methods:{
			sentmsg(){
//				console.log(this.msg)
				this.$store.dispatch('sentmsg',this.msg1)
			}
		},
		
		mounted(){
			var that = this
			axios.get('static/json/details1.json').then(function(item){
				item.data.forEach(function(item,i){					
					if( that.msg==item.id ){
						that.msg1=item
//						console.log(that.msg1)
					}
				})
			})
		}
	}
</script>

<style scoped lang="scss">
	#detailfooter{
		background:#fff;
		position:absolute;
		bottom: 0;left:0;
		font-size:10px;
		z-index:10000;
		height:45px;
		display: flex;
		flex-direction:row;
		width:100%;
		border-top:1px solid #eee;
		.other{
			width:40%;
			display:flex;
			flex-direction: row;
			i{
				line-height: 44px;
			}
			li {
				width:33%;
				a{
					display: flex;
					margin-top:5px;
					height:35px;
					flex-direction: column;
					width:100%;
					text-align: center;
					justify-content: space-around;
				}
				
			}
			.numposition{
				position:relative;
				.num{
					display: block;
					position: absolute;
					background:#f60;
					width:15px;
					height:15px;
					line-height:15px;
					text-align: center;
					top:5px;
					color:#fff;
					left:30px;
					border-radius: 10px;
				}
			}
		}
		.add{
			width:60%;
			display: flex;
			height:50px;
			flex-direction: row;
			li{
				width:50%;
				a{
					display: block;
					width:100%;
					height:45px;
					line-height: 45px;
					text-align: center;
				}
				.addcar{
					    background: rgb(255,167,71);
					    color:#fff;
					    font-size:14px;
				}
				.buy{
					background:#f60;
					font-size:14px;
					color:#fff;
				}
			}
		}
	}
	.middle_line {
	    display: inline-block;
	    width: 1px;
	    height: 20px;
	    background-color: #999;
	    -webkit-transform: scale(0.5,1);
	    transform: scale(0.5,1);
	    float: right;
	    margin-top: 14px;
	}
</style>


