
<template>
	<div>
		<header>
	    	<img @click="back" src="static/img/vip/headChevronLeft.png"/>
	    	<span>购物车</span>
	    	<img src="static/img/vip/headerIconMore.png"/>
	    </header>
	    
	    <div class="null"  v-if="!value.length">
		    <img class="goodnull" src="static/image/empty_cart2.png">
		    <router-link to="/waimai">去逛逛</router-link>
	    </div>
	    
		<div class="goodsbox" v-for="(good,index) in value" :key="index">
			<img :src="good.src1"/>
			<span class="iconfont icon-iconfonticonfontdelete" @click="deletegood(good.id)"></span>
			<div class="goodTitle" >
				<p>
					[鲜花]：<span>{{good.name}}</span>
				</p>
				<p>
					现价：<span class="price">{{good.new}}</span>
				</p>
				<div class="goodnum">数量：
					<a @click="reducenum(good.id)">-</a>
					<span>{{good.num}}</span>
					<a @click="addnum(good.id)">+</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import {mapState,mapGetters,mapActions} from 'vuex'
export default{
	name: 'showcar',
	data(){
		return{
			details:{}
		}
	},
	computed:{
		...mapState({
			value:"goods"
		}),
	},
	methods:{
		addnum(item){
			this.$store.dispatch('addnumber',item)
		
		},
		reducenum(item){
			this.$store.dispatch('reducenumber',item)
		
		},
		deletegood(item){
			this.$store.dispatch('deletegood',item)
		},
		back(){
			window.history.go(-1)
		}
	},
	mounted(){
		console.log(this.value)
	}
	
}
</script>

<style scoped lang="scss">
header{
	width:100%;
	height:50px;
	background: white;
	display: flex;
	justify-content: space-between;
	padding: 0 10px;
	line-height: 50px;
	img{
		display: inline-block;
		width:30px;
		height:30px;
		margin-top:10px;
	}
}
	.null{
		display:flex;
		width:100%;
		flex-direction: column;
		.goodnull{
			width:70%;
			height:70%;
			margin:15% 0 0 15%;
		}
		a{
			display: inline-block;
			border: 1px solid #f60;
			width:20%;
			padding:2% 0;
			margin-left:40%;
			text-align: center;
			margin-top:10%;
			border-radius:5px;
			color:#f60;
		}
	}
	.goodsbox{
		position: relative;
		font-size:14px;
		border:1px solid #ccc;
		display:flex;
		flex-direction: row;
		width:100%;
		padding:2%;
		height:100%;
		.iconfont{
			position: absolute;
			display: block;
			top: 40px;
			right: 0;
			z-index: 10000;
			width:50px;
			height: 50px;
		}
		img{
			display: block;
			width:20%;
			height:20%;
		}
		.goodTitle{
			padding-left:5%;
			p{
				padding:5% 0;
				.price{
					color:red;
				}
			}
			.goodnum{
				margin-top:5%;
				a{
					border: 1px solid #ccc;
					display: inline-block;
					text-align: center;
					width:20px;
					height:20px;
					line-height:20px;
				}
				span{
					border: 1px solid #ccc;
					display: inline-block;
					text-align: center;
					width:30px;
					height:20px;
					line-height:20px;
				}
			}
		}
	}
</style>


