<template>
	<div class="list">
		<div class="title">
			<div class="title_line"></div>
			<p class="title_name">鲜花精选</p>
		</div>
		<div class="goods">
			<router-link :to='"/detail/"+good.id' class="good" v-for="(good,index) in goods" :key="index">
				<img :src="good.src"/>
				<p>{{good.Class2?good.Class2:good.Class1}}</p>
				<p>{{good.Cpmc}}</p>
				<p>￥{{good.Yhj}}</p>
			</router-link>
		</div>
		<a class="all" href="#">全部鲜花</a>
	</div>
</template>
	
<script>
	import axios from 'axios'
	export default{
		data(){
			return{
				goods:null
			}
		},
		methods:{
			addmsg(){
				var that = this
				axios.get('static/json/list.json').then(function(msg){
					that.goods = msg.data
				})
			}
		},
		mounted(){
			this.addmsg()
		}
	}
	
</script>

<style scoped lang="scss">
	.title_line {
	    width: 160px;
	    height: 1px;
	    background-color: #bbb;
	    margin: 22px auto 0px;
	}
	.title_name {
	    width: 90px;
	    text-align: center;
	    background-color: #fff;
	    font-size: 16px;
	    color: #222;
	    margin: -12px auto 13px;
	}
	.list{
		width:100%;
		padding:0 0 0 8px;
		font-size:12px;
		padding-bottom:20px;
		.all{
			display:inline-block;
			width:25%;
			height:30px;
			border:1px solid #ccc;
			margin-top:20px;
			text-align: center;
			line-height: 28px;
			margin-left:37.8%;
		}
	}
	.goods{
		width:100%;
		.good{
			float:left;
			width:48%;
			margin-left:1%;
			margin-top:1%;
			padding:2% 2% 2% 6%;
			img{
				width:90%;
			}
			p{
				padding:1%;
			}
		}
	}
	.good{
		display:block;
	    border: 1px solid;
	    border-color: rgb(239,239,239);
	}
</style>


