import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


import actions from './actions'
const state = {
	goods:[]
}
const getters = {
	
}
const mutations = {
	sentmsg(state,item){
		if( state.goods!='' ){
			for( var i=0 ;  i<state.goods.length; i++ ){
				if(state.goods[i].id==item.id){
					state.goods[i].num++
					return
				}
			}
		}
		state.goods.push(item)
	},
	addnumber(action,num){
		
		state.goods.forEach(function(item,i){
			if(item.id==num){
				item.num++
			}
		})
		
	},
	reducenumber(action,num){
		
		state.goods.forEach(function(item,i){
			if( item.num==1 ){
				state.goods.splice(i,1)
			}else{
				if(item.id==num){
					item.num--
				}
			}
		})

	},
	
	deletegood(action,num){
		 
		state.goods.forEach(function(item,i){
			if(item.id==num){
				state.goods.splice(i,1)
			}
		})
		 
	}
}


const store = new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})

export default store


